@import './scss/variables.scss';
@import './landing/index.scss';
@import './navbar/index.scss';
@import './find/index.scss';

@import url('https://fonts.googleapis.com/css?family=Josefin+Sans:400,600|Libre+Baskerville|Open+Sans:300,400,600,700,800|Roboto+Mono:400,500&display=swap');

body {
    font-family: 'Open Sans', sans-serif;
}

.inline-input-group {
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input {
        margin: 5px;
        &:first-child {
            margin-left: 0px;
        }
        &:last-child {
            margin-right: 0px;
        }
    }
    @media only screen and (max-width: 728px) {
        flex-direction: column;
    }
}

