.navbar-component {
    color: white;
    background: #0D1E23;
    padding: 20px;
    z-index: 1;
    @media only screen and (max-width: 728px) {
        padding: 15px 10px;
    }
    a {
        color: #606f74;
        font-size: 1.2rem;
        margin: auto 10px;
        font-weight: 600;
        text-decoration: none;
        &:hover, &.selected {
            text-decoration: none;
            color: white;
        }
        &:active {
            text-decoration: none;
        }
    }
    .container {
        margin: auto;
        max-width: 920px;
    }
}
