    .card {
        margin: auto;
        text-align: center;
        width: 100%;
        background: white;
        padding: 30px 30px;
        box-shadow: 0 2px 40px 0 rgba(0,0,0,.1);
        border-radius: 5px;
        width: auto;
        max-width: 900px;
        position: relative;
        @media only screen and (max-width: 728px) {
            border-radius: 0px;
            box-shadow: 0 2px 30px 0 rgba(0,0,0,0);
            padding: 30px 10px;
        }
        .container {
            max-width: 728px;
            margin: auto;
            h1 {
                font-weight: 300;
                font-size: 2.5rem;
                font-family: 'Libre Baskerville', serif;
            }
            h3 {
                max-width: 500px;
                margin: auto;
                opacity: 0.6;
                font-weight: 400;
                font-size: 1.2rem;
            }
            form {
                padding: 10px;
                input, button {
                    font-weight: 600;
                    width: 100%;
                    border-radius: 5px;
                    border: none;
                    outline: none;
                    padding: 7px 15px;
                    box-shadow: 0 2px 4px 0 rgba(14,30,37,.01);
                    transition: background-color,border,box-shadow,-webkit-box-shadow;
                    box-sizing: border-box;
                    border: 2px solid rgba(0, 0, 0, 0.1);
                }
                button {
                    background-color: $main-color;
                    color: white;
                    margin-top: 10px;
        
                    &:hover{
                        background-color: lighten($main-color, 5%);
                    }
                }
                input {
                    font-family: 'Roboto Mono', monospace;
                    border-radius: 100px;
                    text-align: left;
                    position: relative;
                    overflow: hidden;
                    // &::before {
                    //     content: '#';
                    //     height: 40px;
                    //     width: 30px;
                    //     top: 0px;
                    //     bottom: 0px;
                    //     left: 10px;
                    //     right: 0px;
                    //     padding: 10px;
                    //     margin: 10px;
                    //     position: absolute;
                    //     background-color: #f4bb00;
                    //     color: white;
                    // }
                    &:hover {
                        border-color: rgba(0, 0, 0, 0.3);
                    }
                    &:focus {
                        border-color: #f4bb00;
                    }
                    &::placeholder {
                        opacity: 0.5;
                    }
                    &:disabled {
                        opacity: 0.4;
                        cursor: not-allowed;

                        &:hover{
                            border-color: rgba(0, 0, 0, 0.1);
                        }
                    }
                }
            }
        }
    }
}